import { Swiper, SwiperSlide } from 'swiper/react'
import SwiperCore, { Navigation, Pagination, Scrollbar, A11y } from 'swiper'
import 'swiper/swiper.scss'
import 'swiper/swiper.scss'
import 'swiper/components/navigation/navigation.scss'
import 'swiper/components/pagination/pagination.scss'
import 'swiper/components/scrollbar/scrollbar.scss'
import Project from './Project'

SwiperCore.use([Navigation, Pagination, Scrollbar, A11y]);

const Projects = () => {
  return (
    <section className={'section projects'} id={'projects'}>

      <div className={'section-title'}>
        <h2 className={'section-title__lg'}>
          Projects
        </h2>
        <div className={'section-title__sm'}>
          My pet-projects
        </div>
      </div>

      <div className={'projects__body'}>
        <Swiper
          spaceBetween={0}
          slidesPerView={1}
          navigation
          pagination={{ clickable: true }}
          className={'projects__swiper'}
        >
          <SwiperSlide className={'projects__slide'}>
            <Project
              imgRef={'https://picsum.photos/id/1/600/600'}
              title={'Apple Store'}
              desc={'Fully responsive website with brilliant color pallete!'}
              demoUrl={'https://dalerfrontend.github.io/apple-store/'}
              codeUrl={'https://github.com/dalerfrontend/apple-store'}
            />
          </SwiperSlide>
          <SwiperSlide className={'projects__slide'}>
            <Project
              imgRef={'https://picsum.photos/id/1/600/600'}
              title={'Design Lab'}
              desc={'Another beautiful responsibe website with a lot of features'}
              demoUrl={'https://dalerfrontend.github.io/design-lab/'}
              codeUrl={'https://github.com/dalerfrontend/design-lab'}
            />
          </SwiperSlide>
          <SwiperSlide className={'projects__slide'}>
            <Project
              imgRef={'https://picsum.photos/id/1/600/600'}
              title={'Social Media'}
              desc={'Amazing social media, made using React, Redux and Material UI'}
              demoUrl={'https://dalerfrontend.github.io/social-media/'}
              codeUrl={'https://github.com/dalerfrontend/social-media/'}
            />
          </SwiperSlide>
          <SwiperSlide className={'projects__slide'}>
            <Project
              imgRef={'https://picsum.photos/id/1/600/600'}
              title={'Todo List'}
              desc={'React made web app you can use to increase your productivity'}
              demoUrl={'https://dalerfrontend.github.io/todo/'}
              codeUrl={'https://github.com/dalerfrontend/todo-react'}
            />
          </SwiperSlide>
        </Swiper>
      </div>

    </section>
  )
}

export default Projects
