import { useFormik } from "formik"
import Button from "./Button"
import { firestore } from 'firebaseApp'

const Contact = () => {
  const form = useFormik({
    initialValues: {
      name: '',
      email: '',
      phone: '',
      message: ''
    },
    onSubmit(v) {
      if (v.name && v.email && v.phone && v.message) {
        firestore.collection('resume-messages')
          .add(v)
          .then(() => {
            form.resetform()
          })
          .catch(() => console.log('error'))
      }
    }
  })

  return (
    <section className={'contact section'} id={'contact'}>

      <div className={'section-title'}>
        <h2 className={'section-title__lg'}>
          Contact Me
        </h2>
        <div className={'section-title__sm'}>
          Get in touch
        </div>
      </div>

      <div className={'contact__body'}>

        <div className={'contact__left'}>
          <ul className={'contact__contact-methods'}>

            <li className={'contact__method'}>
              <i className={`uil uil-phone contact__method-icon`}></i>
              <div className={'contact__method-main'}>
                <div className={'contact__method-text-lg'}>
                  Call Me
                </div>
                <div className={'contact__method-text-sm'}>
                  +998 91 400-74-39
                </div>
              </div>
            </li>

            <li className={'contact__method'}>
              <i className={`uil uil-envelope contact__method-icon`}></i>
              <div className={'contact__method-main'}>
                <div className={'contact__method-text-lg'}>
                  Email
                </div>
                <div className={'contact__method-text-sm'}>
                  dalerchess@mail.ru
                </div>
              </div>
            </li>

            <li className={'contact__method'}>
              <i className={`uil uil-location-point contact__method-icon`}></i>
              <div className={'contact__method-main'}>
                <div className={'contact__method-text-lg'}>
                  Location
                </div>
                <div className={'contact__method-text-sm'}>
                  Bukhara, Yangiobod
                </div>
              </div>
            </li>

          </ul>
        </div>

        <div className={'contact__right'}>
          <form className={'contact__form'}>
            <input
              name={'name'}
              className={'contact__input contact__input-name'}
              placeholder={'Name'}
              value={form.values.name}
              onChange={form.handleChange}
            />
            <input
              name={'email'}
              className={'contact__input contact__input-email'}
              placeholder={'Email'}
              value={form.values.email}
              onChange={form.handleChange}
            />
            <input
              name={'phone'}
              className={'contact__input contact__input-phone'}
              placeholder={'Phone'}
              value={form.values.phone}
              onChange={form.handleChange}
            />
            <textarea
              name={'message'}
              className={'contact__input contact__input-message'}
              placeholder={'Message'}
              value={form.values.message}
              onChange={form.handleChange}
            >
            </textarea>
          </form>
          <Button
            color={'primary'}
            children={'Send Message'}
            Icon={'uil uil-message'}
            rootClasses={'contact__send-btn'}
            onClick={form.handleSubmit}
          />
        </div>

      </div>

    </section>
  )
}

export default Contact
