import { useState } from "react"
import { useEffect } from "react"

const useLinks = () => {
  const [selected, setSelected] = useState(0)

  useEffect(() => {
    window.addEventListener('scroll', () => {
      const sections = Array.from(document.querySelectorAll('section'))
      for (let i = 0; i < sections.length; i++) {
        const element = sections[i];
        if (element.getBoundingClientRect().y < 50) {
          if (selected !== i) {
            setSelected(i)
          }
        }
      }
    })
  }, [])

  const links = [
    { to: '#home', text: 'Home', icon: 'uil uil-estate' },
    { to: '#about', text: 'About', icon: 'uil uil-user-circle' },
    { to: '#skills', text: 'Skills', icon: 'uil uil-book-open' },
    { to: '#projects', text: 'Projects', icon: 'uil uil-image' },
    { to: '#contact', text: 'Contact', icon: 'uil uil-message' },
  ]

  return {
    selected,
    links,
    setSelected
  }
}

export default useLinks
